import React from 'react'
import { Pagination } from 'react-materialize'

import Note from './Note'

class List extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			page: 1,
			notesPerPage: 10
		}
	}

	handleSelect = async (selectedKey) => {
		if (selectedKey !== undefined || selectedKey !== null) {
			return this.setState({ page: selectedKey })
		}
		return this.setState({  })
	}

	render() {
		let key = 1
		const filter = this.props.filter
		const allNotes = this.props.notes.filter(function (note) {
			return note.otsikko !== undefined || null
		})

		let notesToShow = []
		if (filter && (filter !== undefined || null || '')) {
			try {
				const filterMatchesTitle = allNotes.filter(note => note.otsikko.toLowerCase().includes(filter.toLowerCase())).slice(0,10)
				const filterMatchesTag = allNotes.filter(function (note) {
					return note.tagit[0] !== undefined && note.tagit.join(' ').includes(filter)
				})
				notesToShow = filterMatchesTag.concat(filterMatchesTitle)
				.slice()
				.sort(function(a,b) {
					return a.id > b.id
				})
				.filter(function(el, index, self) {
					return index === self.indexOf(el)
				})
			} catch (e) {
				// Happens everytime after there is filter when deleting note
				console.log(e)
			}
		} else {
			notesToShow = allNotes
		} 

		const start = (this.state.page-1)*this.state.notesPerPage
		const end = start+this.state.notesPerPage
		notesToShow = notesToShow.slice(start, end)


		return (
			<div className="container">
					<div className="center">
						<Pagination items={Math.ceil(this.props.notes.length/this.state.notesPerPage)} activePage={this.state.page} maxButtons={10} onSelect={this.handleSelect} />
					</div>
					<ul>
						{notesToShow.map(note => <li key={key++}>
						<div>
							<Note note={note} Link={this.props.Link} Key={key} />
							</div></li>
						)}
					</ul>
					<div className="center">
						<Pagination items={Math.ceil(this.props.notes.length/this.state.notesPerPage)} activePage={this.state.page} maxButtons={10} onSelect={this.handleSelect} />
					</div>
			</div>
		)
	}
}

export default List
